/* eslint-disable react-hooks/exhaustive-deps */
import React, {useState, useEffect, useRef} from "react";
import imageProfil from "../Images/profil.jpeg";
import Terminal from "../Terminal/Terminal";
import {images, recImg} from "../Images/Images";
import data from "../../data.json";
import Carousel from "../Carusel/Carousel";
import Modal from "../Modal/Modal";
import "./Main.css";
import recommendations from "../../recomend.json";
import Statistics from "../Statistics/Statistics";
import EmailForm from "../EmailForm/EmailForm";
import Experience from "../Experience/Experience";
import Footer from "../Footer/Footer";
import UserIP from "./UserIP";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from "chart.js";

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
);

function Mainer() {
    const [isEmailFormVisible, setIsEmailFormVisible] = useState(false);
    const [selectedProject, setSelectedProject] = useState(null);
    const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const [workshopAssistantCount, setWorkshopAssistantCount] = useState(0);
    const [numberOfStudentsCount, setNumberOfStudentsCount] = useState(0);
    const [interAssistantCount, setinterAssistantCount] = useState(0);
    const [startCounting] = useState(false);
    const [useTerminal, setUseTerminal] = useState(false);
    const [isAboutVisible, setIsAboutVisible] = useState(false);
    const [expandedProjectId, setExpandedProjectId] = useState(null);
    const projectsContainerRef = useRef(null);
    const toggleDescription = (id) => {
        if (expandedProjectId === id) {
            setExpandedProjectId(null);
        } else {
            setExpandedProjectId(id);
        }
    };

    const handleAuthentication = () => {
        setIsEmailFormVisible(false);
    };

    const disableBodyScroll = (disable) => {
        if (disable) {
            document.body.style.overflow = "hidden";
        } else {
            document.body.style.overflow = "auto";
        }
    };

    const preloadImages = () => {
        images.forEach((src) => {
            const img = new Image();
            img.src = src;
        });
        recImg.forEach((src) => {
            const img = new Image();
            img.src = src;
        });
    };

    const dataGraph = {
        labels: [
            "Jul",
            "Aug",
            "Sep",
            "Oct",
            "Nov",
            "Dec",
            "Jan",
            "Feb",
            "Mar",
            "Apr",
        ],
        datasets: [
            {
                label: "Contributions",
                data: [36, 166, 30, 5, 59, 45, 59, 36, 40, 21],
                borderColor: "#3f8a89",
                backgroundColor: "#65e7e0",
            },
        ],
    };

    const options = {
        plugins: {
            legend: {
                labels: {
                    color: "white",
                    font: {
                        size: 14,
                        family: "Helvetica, Arial, sans-serif",
                        style: "normal",
                    },
                },
            },
            title: {
                display: true,
                text: "Monthly Contributions in Projects",
                color: "white",
                font: {
                    size: 18,
                    family: "Helvetica, Arial, sans-serif",
                },
            },
        },
        scales: {
            y: {
                beginAtZero: true,
                grid: {
                    color: "rgba(255, 255, 255, 0.1)",
                },
                ticks: {
                    color: "white",
                },
            },
            x: {
                grid: {
                    color: "rgba(255, 255, 255, 0.1)",
                },
                ticks: {
                    color: "white",
                },
            },
        },
        elements: {
            line: {
                borderColor: "white",
                borderWidth: 2,
            },
            point: {
                borderColor: "white",
                backgroundColor: "white",
            },
        },
    };

    useEffect(() => {
        const handleScroll = () => {
            const mainDiv = document.getElementById("main");
            if (window.scrollY >= window.innerHeight / 2.7) {
                mainDiv.classList.add("show");
                mainDiv.classList.remove("close");
            } else {
                mainDiv.classList.remove("show");
                mainDiv.classList.add("close");
            }
        };
        window.addEventListener("scroll", handleScroll);

        return () => window.removeEventListener("scroll", handleScroll);
    }, []);


    useEffect(() => {
        const handleScroll = () => {
            checkVisibility();
        };

        const checkVisibility = () => {
            const aboutSection = document.getElementById("about");
            if (!aboutSection) return;

            const screenPosition = window.innerHeight;
            const aboutPosition = aboutSection.getBoundingClientRect().top;
            const visibilityThreshold = window.innerHeight * 0.6;

            if (aboutPosition < screenPosition - visibilityThreshold) {
                setIsAboutVisible(true);
            } else {
                setIsAboutVisible(false);
            }
        };

        checkVisibility();
        window.addEventListener("scroll", handleScroll);
        preloadImages();
        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, []);

    useEffect(() => {
        const handleResize = () => {
            setWindowWidth(window.innerWidth);
        };
        window.addEventListener("resize", handleResize);
        return () => {
            window.removeEventListener("resize", handleResize);
        };
    }, []);

    useEffect(() => {
        if (startCounting) {
            const maxWorkshopCount = 37;
            const workshopInterval = setInterval(() => {
                if (workshopAssistantCount < maxWorkshopCount) {
                    setWorkshopAssistantCount((prevCount) => prevCount + 1);
                } else {
                    clearInterval(workshopInterval);
                }
            }, 100);

            return () => clearInterval(workshopInterval);
        }
    }, [workshopAssistantCount, startCounting]);

    useEffect(() => {
        if (startCounting) {
            const maxStudentCount = 300;
            const studentInterval = setInterval(() => {
                if (numberOfStudentsCount < maxStudentCount) {
                    setNumberOfStudentsCount((prevCount) => prevCount + 1);
                } else {
                    clearInterval(studentInterval);
                }
            }, 0.1);

            return () => clearInterval(studentInterval);
        }
    }, [numberOfStudentsCount, startCounting]);

    useEffect(() => {
        if (startCounting) {
            const maxInternationalCount = 3;
            const internationalInterval = setInterval(() => {
                if (interAssistantCount < maxInternationalCount) {
                    setinterAssistantCount((prevCount) => prevCount + 1);
                } else {
                    clearInterval(internationalInterval);
                }
            }, 700);

            return () => clearInterval(internationalInterval);
        }
    }, [interAssistantCount, startCounting]);

    const openModal = (project) => {
        setSelectedProject(project);
        windowWidth > 1100 && disableBodyScroll(true);

        requestAnimationFrame(() => {
            const modalContent = document.querySelector(".modal-content");
            if (modalContent) {
                const modalTop =
                    modalContent.getBoundingClientRect().top + window.scrollY;

                window.scrollTo({
                    top: modalTop,
                    behavior: "smooth",
                });
            }
        });
    };

    const closeModal = () => {
        setSelectedProject(null);
        disableBodyScroll(false);
    };

    function AnimatedRole() {
        const roles = ["Software Engineer", "Java Backend Developer", "Microservices Builder", "Problem Solver"];
        const [currentRole, setCurrentRole] = useState("");
        const [index, setIndex] = useState(0);
        const [subIndex, setSubIndex] = useState(0);
        const [reverse, setReverse] = useState(false);
        const [roleIndex, setRoleIndex] = useState(0);

        useEffect(() => {
            if (index === roles.length) setIndex(0);
            const currentIndex = roles[roleIndex % roles.length];

            if (subIndex === currentIndex.length + 1 && !reverse) {
                setReverse(true);
                setTimeout(() => {
                    setSubIndex((prev) => prev - 1);
                }, 1000);
                return;
            }

            if (subIndex === 0 && reverse) {
                setReverse(false);
                setRoleIndex((prev) => prev + 1);
                setIndex((prev) => prev + 1);
                return;
            }

            const timeout = setTimeout(
                () => {
                    setSubIndex((prev) => prev + (reverse ? -1 : 1));
                },
                reverse ? 110 : 70
            );

            return () => clearTimeout(timeout);
        }, [subIndex, index, reverse, roleIndex, roles]);

        useEffect(() => {
            setCurrentRole(roles[roleIndex % roles.length].substring(0, subIndex));
        }, [subIndex, roleIndex, roles]);

        return <span className="animated-role">{currentRole}</span>;
    }

    const mainBackgroundStyle =
        selectedProject && windowWidth > 1100
            ? {
                backgroundColor: "rgba(0, 0, 0, 0.75)",
                transition: "background-color 0.5s ease",
            }
            : {};

    return (
        <main style={mainBackgroundStyle}>
            <UserIP/>
            {isEmailFormVisible && (
                <EmailForm onAuthentication={handleAuthentication}/>
            )}
            <div id="main" className="close">
                <div id="about" className={`show ${isAboutVisible ? "visible" : ""}`}>
                    <img src={imageProfil} alt="Profile" className="profile-image"/>
                    <h1>Hi I'm Harut a </h1>
                    <h1>
                        <AnimatedRole/>
                    </h1>
                    <p id="paragraph" className="karaoke-text">
                        I’m a software engineer with experience in backend development.
                        I work with Java, Spring Boot, and PostgreSQL, and I create microservices from scratch.
                        I also know JavaScript, CI/CD, Docker, and have knowledge of Kubernetes.
                        I enjoy improving my code and finding better solutions.
                        I have also taught students and shared my experience as a software engineer.
                    </p>
                    <a
                        href={`${process.env.PUBLIC_URL}/Resume.pdf`}
                        download="Harut_CV_Resume.pdf"
                        className="download-cv-button"
                    >
                        Download CV
                    </a>

                    <br/><br/>

                    {/* Show commands only when not inside "see all information" */}
                    {!useTerminal && (
                        <div>
                            If you want to see information about me, write commands in terminal:<br/><br/>
                            <div className="terminalinfo">
                                <table className="terminal-table" role="table" aria-label="Terminal commands">
                                    <thead>
                                    <tr>
                                        <th>Command</th>
                                        <th>Description</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr>
                                        <td><code>cat</code></td>
                                        <td>Read file</td>
                                    </tr>
                                    <tr>
                                        <td><code>ls</code></td>
                                        <td>See files/folders</td>
                                    </tr>
                                    <tr>
                                        <td><code>clear</code></td>
                                        <td>Clear terminal</td>
                                    </tr>
                                    <tr>
                                        <td><code>cd</code></td>
                                        <td>Go into/out from folder</td>
                                    </tr>
                                    </tbody>
                                </table>

                                <p className="terminal-hint">
                                    or{" "}
                                    <span
                                        className="clickable-text"
                                        onClick={() => setUseTerminal(true)}
                                        tabIndex={0}
                                        onKeyDown={(e) => (e.key === "Enter" || e.key === " ") && setUseTerminal(true)}
                                        aria-label="Click to see all information"
                                    >
            click here
          </span>{" "}
                                    to see all information
                                </p>
                            </div>
                        </div>
                    )}

                    {/* Toggle between Experience and Terminal */}
                    {useTerminal ? (
                        <div>
                            <Experience/>
                            <p className="terminal-hint">
        <span
            className="clickable-text"
            onClick={() => setUseTerminal(false)}
            tabIndex={0}
            onKeyDown={(e) => (e.key === "Enter" || e.key === " ") && setUseTerminal(false)}
            aria-label="Go back to terminal commands"
        >
          ← Go back to commands
        </span>
                            </p>
                        </div>
                    ) : (
                        <Terminal/>
                    )}
                </div>

                <Statistics
                    windowWidth={windowWidth}
                    dataGraph={dataGraph}
                    options={options}
                    workshopAssistantCount={workshopAssistantCount}
                    numberOfStudentsCount={numberOfStudentsCount}
                    interAssistantCount={interAssistantCount}
                    recommendations={recommendations}
                    recImg={recImg}
                />
                <div id="carusello">
                    <Carousel
                        items={data.map((item, index) => ({
                            id: item.id,
                            src: images[index],
                            alt: `Image ${item.id}`,
                        }))}
                        active={0}
                    />
                </div>

                {/* Projects Section */}

                <div ref={projectsContainerRef} className="projects-container">
                    {data.map((project, index) => (
                        <div
                            key={project.id}
                            className="project"
                            onClick={() => {
                                openModal({...project, src: images[index]});
                            }}
                            style={{
                                height: expandedProjectId === project.id ? "auto" : "32.5em",
                            }}
                        >
                            <div className="project-image">
                                <img src={images[index]} alt={`Project ${project.id}`}/>
                            </div>
                            <div className="project-info">
                                <h2>{project.name}</h2>
                                <p>
                                    {expandedProjectId === project.id
                                        ? project.descriptionl
                                        : `${
                                            project.descriptionl?.substring(0, 100) ?? ""
                                        }... `}{" "}
                                    <span
                                        className="read-more"
                                        onClick={(e) => {
                                            e.stopPropagation();
                                            toggleDescription(project.id);
                                        }}
                                    >
                    {expandedProjectId === project.id
                        ? "Read Less"
                        : "Read More"}
                  </span>
                                </p>
                                <a
                                    href={project.url}
                                    className="link"
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    onClick={(e) => e.stopPropagation()}
                                >
                                    View Project
                                </a>
                            </div>
                        </div>
                    ))}
                </div>
                {windowWidth > 1100 && selectedProject && (
                    <Modal
                        show={selectedProject}
                        onClose={closeModal}
                        project={selectedProject}
                    />
                )}
                <Footer/>
            </div>
        </main>
    );
}

export default Mainer;
